.App {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.query-body {
  text-align: center;
  max-width: 400px;
}
.query-body .header {
  font-size: xxx-large;
  font-weight: lighter;
}
.query-body .selector {
  display: flex;
}
.query-body .selector > input {
  flex-grow: 1;
}
.query-body .result {
  font-size: 11pt;
}
.query-body .result .success {
}
.query-body .result .warning {
  color: darkorange;
}
.query-body .result .error {
  color: darkred;
}
